import React from 'react'

import {Menu, Segment, Grid, Divider} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';

import IndustryConsumer1 from "./IndustryConsumer1";

import TableData from './table/TableData'
import FloatMenu from "../test/FloatMenu";

class ConsumerSiderMenu1 extends React.Component {

    constructor(props) {
        super(props)
        this.state = { activeItem: '',  }
    }

    handleItemClick = (e, {name}) => {
        this.setState({ activeItem: name })
    }

    render() {
        var industries = ['', '', '', '', '', '', '', '',
                            '', '', '', '', '', '', '', '',
                            '', '', '', '', '']

        return (
            <Segment className="myPusher" style={{height: (document.body.clientHeight - 48).toString() + 'px'}}>
                <FloatMenu/>
                <Divider/>
                <Grid>
                        <Grid.Column width={4}>
                            <Menu vertical  tabular color='black'>

                                <Menu.Item header color='purple'></Menu.Item>

                                {industries.map((item) => {
                                    return <Menu.Item
                                        name={item}
                                        active={this.state.activeItem === item}
                                        onClick={this.handleItemClick}
                                        color='violet'
                                    />
                                    }

                                )}

                            </Menu>
                        </Grid.Column>
                    <Grid.Column stretched width={12}>
                        {console.log(this.props.match.params.id)}
                        <IndustryConsumer1 techName={TableData.techNames[this.props.match.params.id - 1]}
                                           techConsumer={TableData.techConsumers[this.props.match.params.id - 1]}
                                           techPrice={TableData.techPrices[this.props.match.params.id - 1]}
                                           techDate={TableData.techDates[this.props.match.params.id - 1]}
                                           techKeyword={TableData.techKeywords[this.props.match.params.id - 1]}
                                           techDescription={TableData.techDescriptions[this.props.match.params.id - 1]}/>
                    </Grid.Column>

                </Grid>
            </Segment>
        )
    }



}

export default ConsumerSiderMenu1
