import React from 'react'

import {Menu, Segment, Grid, Divider} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';

import IndustryProvider1 from "./IndustryProvider1";

import TableData from './table/TableData'
import FloatMenu from "../test/FloatMenu";


class ProviderSiderMenu1 extends React.Component {

    constructor(props) {
        super(props)
        this.state = { activeItem: '',  }
    }

    handleItemClick = (e, {name}) => {
        this.setState({ activeItem: name })
    }

    render() {
        var industries = ['', '', '', '', '', '', '', '',
                            '', '', '', '', '', '', '', '',
                            '', '', '', '', '']

        return (
            <Segment className="myPusher" style={{height: (document.body.clientHeight - 48).toString() + 'px'}}>
                <FloatMenu/>
                <Divider/>
                <Grid>
                        <Grid.Column width={4}>
                            <Menu vertical  tabular color='black'>

                                <Menu.Item header color='purple'></Menu.Item>

                                {industries.map((item) => {
                                    return <Menu.Item
                                        name={item}
                                        active={this.state.activeItem === item}
                                        onClick={this.handleItemClick}
                                        color='violet'
                                    />
                                    }

                                )}
                            </Menu>
                        </Grid.Column>
                    <Grid.Column stretched width={12}>
                        {console.log(this.props.match.params.id)}
                        <IndustryProvider1 techName={TableData.techNames[this.props.match.params.id - 1]}
                                           patentNum={TableData.patentNums[this.props.match.params.id - 1]}
                                           techType={TableData.techTypes[this.props.match.params.id - 1]}
                                           techSource={TableData.techSources[this.props.match.params.id - 1]}
                                           techOrg={TableData.techOrgs[this.props.match.params.id - 1]}
                                           techIndustry={TableData.techIndustrys[this.props.match.params.id - 1]}
                                           techDate={TableData.techDates[this.props.match.params.id - 1]}
                                           techImage={TableData.techImages[this.props.match.params.id - 1]}
                                            techImageIndex={this.props.match.params.id}/>
                    </Grid.Column>
                </Grid>
            </Segment>
        )
    }
}

export default ProviderSiderMenu1
