import { Swiper } from 'zent';

import React from 'react';

import 'zent/css/index.css';


import bicycle from "../../assets/images/bicycle.jpeg"
import forestry from "../../assets/images/forestry.jpeg"
import livestock from "../../assets/images/livestock.jpeg"
import metallurgy from "../../assets/images/metallurgy.jpeg"
import petrochemical from "../../assets/images/petrochemical.jpeg"
import pharmacy from "../../assets/images/pharmacy.jpeg"
import textile from "../../assets/images/textile.jpeg"
import waterpure from "../../assets/images/waterprocess.jpg"


import {Divider, Grid, Icon, Segment} from 'semantic-ui-react'

import 'semantic-ui-css/semantic.min.css';

import { Card, Col, Row } from 'antd';
import 'antd/dist/antd.css';

const { Meta } = Card;

const pages = [0, 1, 2, 3, 4, 5, 6, 7];
const imageList = [bicycle, forestry, waterpure, livestock, metallurgy, petrochemical, pharmacy, textile]
const chineseTitles = ['', '', '',
    '', '', '', '',
    'Eurojersey']
const englishTitles = ['The story of Cyclotron and bicycle', 'The Old Smartphone becomes Forest Keeper', '2019 WATERTECH Guangdong',
    'Texas livestock exhibitors', 'The World-top Iron Engineers gathering in ShanDong Province', 'AI Enhances the development of petroleum exploration technology',
    'Be The Match BioTherapies Launches Program for Faster Cell Facility Onboarding', 'Eurojersey and Polytechnic university of milan holds Design Competition']

class SimpleSwiper extends React.Component {

  constructor() {
    super();
    this.state = {
      current: 0,
      prev: 7,
        language:localStorage.getItem('language')
    }
  }

  handleChange = (current, prev) => {
    this.setState({ current, prev });
  }

  onClick = () => {
      window.location.href = 'http://localhost:3000/expect'
  }

  render() {
    return (
      <div>



                <Divider horizontal style={{textAlign:'center', fontSize:'40px', color:'#6258b5', }}>{this.state.language === '' ? '': 'News and Highlights'}</Divider>

          <Grid container stackable verticalAlign='middle'>
              <Grid.Row>
              <Grid.Column textAlign='right' >
                <a href='/expect' style={{textAlign:'right', color:'#6258b5'}}>{this.state.language === '' ? '': 'More'}<Icon name='angle double right' /></a>
              </Grid.Column>
              </Grid.Row>

          </Grid>
          <Segment>
        <Swiper
          className="swiper-demo-simple"
          dots={false}
          arrows
          autoplay
          onChange={this.handleChange}
        >
          {
            pages.map((item, index) => {
              return (
                <div  key={index}>
                    <Row gutter={16}>
                        <Col span={8}>
                            <Card as="a" href="/expect" hoverable style={{ width: 400 }} onClick={this.onClick}
                                  cover={<img alt="example" src={imageList[index]} style={{height:'300px', width:'400px'}}/>}>
                                <Meta title={this.state.language === '' ? chineseTitles[index]: englishTitles[index]}
                                      style={{textAlign:'center'}} />
                            </Card>
                        </Col>
                        <Col span={8}>
                            <Card as="a" href="/expect" hoverable style={{ width: 400 }} onClick={this.onClick}
                                  cover={<img alt="example" src={imageList[(index + 1) % 8]} style={{height:'300px', width:'400px'}}/>}>
                                <Meta title={this.state.language === '' ? chineseTitles[(index + 1) % 8]: englishTitles[(index + 1) % 8]}
                                      style={{textAlign:'center'}} />
                            </Card>
                        </Col>
                        <Col span={8}>
                            <Card as="a" href="/expect" hoverable style={{ width: 400 }} onClick={this.onClick}
                                  cover={<img alt="example" src={imageList[(index + 2) % 8]} style={{height:'300px', width:'400px'}}/>}>
                                <Meta title={this.state.language === '' ? chineseTitles[(index + 2) % 8]: englishTitles[(index + 2) % 8]}
                                      style={{textAlign:'center'}} />
                            </Card>
                        </Col>
                    </Row>
                </div>
              );
            })
          }
        </Swiper>
        {/* current={current}
        prev={prev}
        {
          typeof current === 'number' && typeof prev === 'number' &&
            <div style={{textAlign:'center'}}> {prev + 1}  {current + 1}</div>
        } */}
          </Segment>
      </div>
    );
  }
}

export default SimpleSwiper;
