class ProviderSiderMenu0 extends React.Component {

    constructor(props) {
        super(props)
        this.state = { activeItem: '',  }
    }

    handleItemClick = (e, {name}) => {
        this.setState({ activeItem: name })
    }

    render() {
        var industries = ['', '', '', '', '', '', '', '',
                            '', '', '', '', '', '', '', '',
                            '', '', '', '', '']
        console.log(this.props.ind)

        return (
            <Segment>
                <Divider/>
                <Grid>
                        <Grid.Column width={4}>
                            <Menu vertical  tabular color='black'>

                                <Menu.Item header color='purple'></Menu.Item>

                                {industries.map((item) => {
                                    return <Menu.Item
                                        name={item}
                                        active={this.state.activeItem === item}
                                        onClick={this.handleItemClick}
                                        color='violet'
                                    />
                                    }
                                )}
                            </Menu>
                        </Grid.Column>
                    <Grid.Column stretched width={12}>

                        <IndustryProvider0/>
                    </Grid.Column>

                </Grid>
            </Segment>
        )
    }
}

export default ProviderSiderMenu0
