import React from 'react'
import { Segment, Grid, Header, List, Container, Statistic, Image} from 'semantic-ui-react'

import 'semantic-ui-css/semantic.min.css';

import technology from './technology_count.jpg'

class ComposeFooter extends React.Component {

    tick = () => {
        // console.log('timer ', this.state.techNum)
        localStorage.setItem('techNum', parseInt(localStorage.getItem('techNum')) + 1)
        this.setState({
            language:localStorage.getItem('language'),
            techNum:parseInt(localStorage.getItem('techNum'))
        })

    }

    componentDidMount() {
        this.timer = setInterval(this.tick, 2000);
    }
    componentWillUnmount() {
        //unmount
        clearInterval(this.timer);
    }

    constructor() {
        super();
        if(localStorage.getItem('techNum') == null) {
            localStorage.setItem('techNum', 18300)
        }
        this.state = {
            language:localStorage.getItem('language'),
            techNum:parseInt(localStorage.getItem('techNum'))
        }

    }
    render() {
        return (
            <Segment inverted vertical style={{ padding: '5em 0em' }}>
      <Container>
        <Grid  inverted stackable>
          <Grid.Row>
            <Grid.Column width={2}>
              <Header inverted as='h4' content={this.state.language === '' ? '': 'About Us'} />
              <List link inverted>
                  <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Development History'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Organization Culture'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Career Opportunity'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Company News'}</List.Item>
                  <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Contact Us'}</List.Item>
              </List>
            </Grid.Column>
            <Grid.Column width={2}>
              <Header inverted as='h4' content={this.state.language === '' ? '': 'Partnership Programs'} />
              <List link inverted>
                <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Global Partner'}</List.Item>
                <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Regional Partner'}</List.Item>
                <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Yearly Partner'}</List.Item>
                <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Customized Program'}</List.Item>
              </List>
            </Grid.Column>
              <Grid.Column width={2}>
                  <Header inverted as='h4' content={this.state.language === '' ? '': 'Account Management'} />
                  <List link inverted>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Login Problems'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'Profile Update'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'More Suggestions'}</List.Item>
                      {/*<List.Item as='a'></List.Item>*/}
                  </List>
              </Grid.Column>
              <Grid.Column width={4}>
                  <Header inverted as='h4' content={this.state.language === '' ? '': 'Development History'} />
                  <List link inverted>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'WAITRO'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'JITRI'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'ZJUT'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'CAU'}</List.Item>
                      <List.Item as='a' href='/expect'>{this.state.language === '' ? '': 'CAS'}</List.Item>
                  </List>
              </Grid.Column>
              <Grid.Column width={3}>
                  <br/><br/><br/>
                  <Statistic inverted size='small' color='blue'>
                      <Statistic.Value color='blue'>

                          <span style={{color:'violet'}}>{parseInt(this.state.techNum / 10000)}</span>
                          <span style={{color:'orange'}}>{parseInt(this.state.techNum / 1000 % 10)}</span>
                          <span style={{color:'yellow'}}>{parseInt(this.state.techNum / 100 % 10)}</span>
                          <span style={{color:'teal'}}>{parseInt(this.state.techNum / 10 % 10)}</span>
                          <span style={{color:'yellow'}}>{parseInt(this.state.techNum  % 10)}</span>

                      </Statistic.Value>
                      {this.state.language === '' ? <Statistic.Label><span style={{fontSize:'20px'}}></span><span style={{fontSize:'22px'}}></span><span style={{fontSize:'25px'}}></span></Statistic.Label>
                          : <Statistic.Label><span style={{fontSize:'5px'}}>pieces   of   technology   access      </span><span style={{fontSize:'15px', color:'yellow'}}>Our&nbsp;platform</span><span style={{fontSize:'15px'}}></span></Statistic.Label>}

                  </Statistic>
              </Grid.Column>
              <Grid.Column width={3}>
              <Image src={technology}  size='medium' inline/>
              </Grid.Column>
          </Grid.Row>
        </Grid>
      </Container>
    </Segment>
        );
    }
}

export default ComposeFooter;
